@import "./colors.scss";
@import "./styles.scss";

.contactContent {
  min-height: 100vh;
  margin: 1em;
  padding: 1em;
  align-content: center;
  .Socials {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-evenly;

    .Developer,
    .Fun {
      display: flex;
      flex-direction: column;
      padding: 2em;
      ul {
        li {
          list-style: none;
          a {
            justify-content: center;
            font-size: 24px;
            padding: 0.25em;
            color: $teal;
            text-decoration: none;
            transition: ease-in-out 0.5s;

            svg {
              font-size: 30px;
              margin-right: 0.5em;
            }
          }
          &:hover {
            a {
              color: $gold;
              transition: ease-in-out 0.5s;
            }
          }
        }
      }
    }
  }
}

@media (max-width: $mobileWidth) {
  .contactContent {
    justify-content: center;
    text-align: center;
    align-items: center;
    .Socials {
      flex-direction: column;
      .Developer,
      .Fun {
        width: 100%;
        flex-direction: row;
        padding: 0;
      }
      .Fun {
        padding: 2em;
      }
    }
  }
}
